	module.exports = {"pathContext":{"docHtml":"<h1>Changelog</h1>\n<h2>v1.6.0 (24/11/2017)</h2>\n<p><em>No changelog for this release.</em></p>\n<hr>\n<h2>v2.0.4 (24/11/2017)</h2>\n<ul>\n<li>[<strong>closed</strong>] Switch to mime-score module for resolving extension contention issues. <a href=\"https://github.com/broofa/node-mime/issues/182\">#182</a></li>\n<li>[<strong>closed</strong>] Update mime-db to 1.31.0 in v1.x branch <a href=\"https://github.com/broofa/node-mime/issues/181\">#181</a></li>\n</ul>\n<hr>\n<h2>v1.5.0 (22/11/2017)</h2>\n<ul>\n<li>[<strong>closed</strong>] need ES5 version ready in npm package <a href=\"https://github.com/broofa/node-mime/issues/179\">#179</a></li>\n<li>[<strong>closed</strong>] mime-db no trace of iWork - pages / numbers / etc. <a href=\"https://github.com/broofa/node-mime/issues/178\">#178</a></li>\n<li>[<strong>closed</strong>] How it works in brownser ? <a href=\"https://github.com/broofa/node-mime/issues/176\">#176</a></li>\n<li>[<strong>closed</strong>] Missing <code>./Mime</code> <a href=\"https://github.com/broofa/node-mime/issues/175\">#175</a></li>\n<li>[<strong>closed</strong>] Vulnerable Regular Expression <a href=\"https://github.com/broofa/node-mime/issues/167\">#167</a></li>\n</ul>\n<hr>\n<h2>v2.0.3 (25/09/2017)</h2>\n<p><em>No changelog for this release.</em></p>\n<hr>\n<h2>v1.4.1 (25/09/2017)</h2>\n<ul>\n<li>[<strong>closed</strong>] Issue when bundling with webpack <a href=\"https://github.com/broofa/node-mime/issues/172\">#172</a></li>\n</ul>\n<hr>\n<h2>v2.0.2 (15/09/2017)</h2>\n<ul>\n<li>[<strong>V2</strong>] fs.readFileSync is not a function <a href=\"https://github.com/broofa/node-mime/issues/165\">#165</a></li>\n<li>[<strong>closed</strong>] The extension for video/quicktime should map to .mov, not .qt <a href=\"https://github.com/broofa/node-mime/issues/164\">#164</a></li>\n<li><a href=\"\"><strong>V2</strong></a> Mime class API <a href=\"https://github.com/broofa/node-mime/issues/163\">#163</a></li>\n<li><a href=\"\"><strong>V2</strong></a> Resolving conflicts over extensions <a href=\"https://github.com/broofa/node-mime/issues/162\">#162</a></li>\n<li>[<strong>V2</strong>] Allow callers to load module with official, full, or no defined types.  <a href=\"https://github.com/broofa/node-mime/issues/161\">#161</a></li>\n<li>[<strong>V2</strong>] Use \"facets\" to resolve extension conflicts <a href=\"https://github.com/broofa/node-mime/issues/160\">#160</a></li>\n<li>[<strong>V2</strong>] Remove fs and path dependencies <a href=\"https://github.com/broofa/node-mime/issues/152\">#152</a></li>\n<li>[<strong>V2</strong>] Default content-type should not be application/octet-stream <a href=\"https://github.com/broofa/node-mime/issues/139\">#139</a></li>\n<li>[<strong>V2</strong>] reset mime-types <a href=\"https://github.com/broofa/node-mime/issues/124\">#124</a></li>\n<li>[<strong>V2</strong>] Extensionless paths should return null or false <a href=\"https://github.com/broofa/node-mime/issues/113\">#113</a></li>\n</ul>\n<hr>\n<h2>v2.0.1 (14/09/2017)</h2>\n<ul>\n<li>[<strong>closed</strong>] Changelog for v2.0 does not mention breaking changes <a href=\"https://github.com/broofa/node-mime/issues/171\">#171</a></li>\n<li>[<strong>closed</strong>] MIME breaking with 'class' declaration as it is without 'use strict mode' <a href=\"https://github.com/broofa/node-mime/issues/170\">#170</a></li>\n</ul>\n<hr>\n<h2>v2.0.0 (12/09/2017)</h2>\n<ul>\n<li>[<strong>closed</strong>] woff and woff2 <a href=\"https://github.com/broofa/node-mime/issues/168\">#168</a></li>\n</ul>\n<hr>\n<h2>v1.4.0 (28/08/2017)</h2>\n<ul>\n<li>[<strong>closed</strong>] support for ac3 voc files <a href=\"https://github.com/broofa/node-mime/issues/159\">#159</a></li>\n<li>[<strong>closed</strong>] Help understanding change from application/xml to text/xml <a href=\"https://github.com/broofa/node-mime/issues/158\">#158</a></li>\n<li>[<strong>closed</strong>] no longer able to override mimetype <a href=\"https://github.com/broofa/node-mime/issues/157\">#157</a></li>\n<li>[<strong>closed</strong>] application/vnd.adobe.photoshop <a href=\"https://github.com/broofa/node-mime/issues/147\">#147</a></li>\n<li>[<strong>closed</strong>] Directories should appear as something other than application/octet-stream <a href=\"https://github.com/broofa/node-mime/issues/135\">#135</a></li>\n<li>[<strong>closed</strong>] requested features <a href=\"https://github.com/broofa/node-mime/issues/131\">#131</a></li>\n<li>[<strong>closed</strong>] Make types.json loading optional? <a href=\"https://github.com/broofa/node-mime/issues/129\">#129</a></li>\n<li>[<strong>closed</strong>] Cannot find module './types.json' <a href=\"https://github.com/broofa/node-mime/issues/120\">#120</a></li>\n<li>[<strong>V2</strong>] .wav files show up as \"audio/x-wav\" instead of \"audio/x-wave\" <a href=\"https://github.com/broofa/node-mime/issues/118\">#118</a></li>\n<li>[<strong>closed</strong>] Don't be a pain in the ass for node community <a href=\"https://github.com/broofa/node-mime/issues/108\">#108</a></li>\n<li>[<strong>closed</strong>] don't make default_type global <a href=\"https://github.com/broofa/node-mime/issues/78\">#78</a></li>\n<li>[<strong>closed</strong>] mime.extension() fails if the content-type is parameterized <a href=\"https://github.com/broofa/node-mime/issues/74\">#74</a></li>\n</ul>\n<hr>\n<h2>v1.3.6 (11/05/2017)</h2>\n<ul>\n<li>[<strong>closed</strong>] .md should be text/markdown as of March 2016 <a href=\"https://github.com/broofa/node-mime/issues/154\">#154</a></li>\n<li>[<strong>closed</strong>] Error while installing mime <a href=\"https://github.com/broofa/node-mime/issues/153\">#153</a></li>\n<li>[<strong>closed</strong>] application/manifest+json <a href=\"https://github.com/broofa/node-mime/issues/149\">#149</a></li>\n<li>[<strong>closed</strong>] Dynamic adaptive streaming over HTTP (DASH) file extension typo <a href=\"https://github.com/broofa/node-mime/issues/141\">#141</a></li>\n<li>[<strong>closed</strong>] charsets image/png undefined <a href=\"https://github.com/broofa/node-mime/issues/140\">#140</a></li>\n<li>[<strong>closed</strong>] Mime-db dependency out of date <a href=\"https://github.com/broofa/node-mime/issues/130\">#130</a></li>\n<li>[<strong>closed</strong>] how to support plist？ <a href=\"https://github.com/broofa/node-mime/issues/126\">#126</a></li>\n<li>[<strong>closed</strong>] how does .types file format look like? <a href=\"https://github.com/broofa/node-mime/issues/123\">#123</a></li>\n<li>[<strong>closed</strong>] Feature: support for expanding MIME patterns <a href=\"https://github.com/broofa/node-mime/issues/121\">#121</a></li>\n<li>[<strong>closed</strong>] DEBUG_MIME doesn't work <a href=\"https://github.com/broofa/node-mime/issues/117\">#117</a></li>\n</ul>\n<hr>\n<h2>v1.3.4 (06/02/2015)</h2>\n<p><em>No changelog for this release.</em></p>\n<hr>\n<h2>v1.3.3 (06/02/2015)</h2>\n<p><em>No changelog for this release.</em></p>\n<hr>\n<h2>v1.3.1 (05/02/2015)</h2>\n<ul>\n<li>[<strong>closed</strong>] Consider adding support for Handlebars .hbs file ending <a href=\"https://github.com/broofa/node-mime/issues/111\">#111</a></li>\n<li>[<strong>closed</strong>] Consider adding support for hjson. <a href=\"https://github.com/broofa/node-mime/issues/110\">#110</a></li>\n<li>[<strong>closed</strong>] Add mime type for Opus audio files <a href=\"https://github.com/broofa/node-mime/issues/94\">#94</a></li>\n<li>[<strong>closed</strong>] Consider making the <code>Requesting New Types</code> information more visible <a href=\"https://github.com/broofa/node-mime/issues/77\">#77</a></li>\n</ul>\n<hr>\n<h2>v1.3.0 (05/02/2015)</h2>\n<ul>\n<li>[<strong>closed</strong>] Add common name? <a href=\"https://github.com/broofa/node-mime/issues/114\">#114</a></li>\n<li>[<strong>closed</strong>] application/x-yaml <a href=\"https://github.com/broofa/node-mime/issues/104\">#104</a></li>\n<li>[<strong>closed</strong>] Add mime type for WOFF file format 2.0 <a href=\"https://github.com/broofa/node-mime/issues/102\">#102</a></li>\n<li>[<strong>closed</strong>] application/x-msi for .msi <a href=\"https://github.com/broofa/node-mime/issues/99\">#99</a></li>\n<li>[<strong>closed</strong>] Add mimetype for gettext translation files <a href=\"https://github.com/broofa/node-mime/issues/98\">#98</a></li>\n<li>[<strong>closed</strong>] collaborators <a href=\"https://github.com/broofa/node-mime/issues/88\">#88</a></li>\n<li>[<strong>closed</strong>] getting errot in installation of mime module...any1 can help? <a href=\"https://github.com/broofa/node-mime/issues/87\">#87</a></li>\n<li>[<strong>closed</strong>] should application/json's charset be utf8? <a href=\"https://github.com/broofa/node-mime/issues/86\">#86</a></li>\n<li>[<strong>closed</strong>] Add \"license\" and \"licenses\" to package.json <a href=\"https://github.com/broofa/node-mime/issues/81\">#81</a></li>\n<li>[<strong>closed</strong>] lookup with extension-less file on Windows returns wrong type <a href=\"https://github.com/broofa/node-mime/issues/68\">#68</a></li>\n</ul>\n<hr>\n<h2>v1.2.11 (15/08/2013)</h2>\n<ul>\n<li>[<strong>closed</strong>] Update mime.types <a href=\"https://github.com/broofa/node-mime/issues/65\">#65</a></li>\n<li>[<strong>closed</strong>] Publish a new version <a href=\"https://github.com/broofa/node-mime/issues/63\">#63</a></li>\n<li>[<strong>closed</strong>] README should state upfront that \"application/octet-stream\" is default for unknown extension <a href=\"https://github.com/broofa/node-mime/issues/55\">#55</a></li>\n<li>[<strong>closed</strong>] Suggested improvement to the charset API <a href=\"https://github.com/broofa/node-mime/issues/52\">#52</a></li>\n</ul>\n<hr>\n<h2>v1.2.10 (25/07/2013)</h2>\n<ul>\n<li>[<strong>closed</strong>] Mime type for woff files should be application/font-woff and not application/x-font-woff <a href=\"https://github.com/broofa/node-mime/issues/62\">#62</a></li>\n<li>[<strong>closed</strong>] node.types in conflict with mime.types <a href=\"https://github.com/broofa/node-mime/issues/51\">#51</a></li>\n</ul>\n<hr>\n<h2>v1.2.9 (17/01/2013)</h2>\n<ul>\n<li>[<strong>closed</strong>] Please update \"mime\" NPM <a href=\"https://github.com/broofa/node-mime/issues/49\">#49</a></li>\n<li>[<strong>closed</strong>] Please add semicolon <a href=\"https://github.com/broofa/node-mime/issues/46\">#46</a></li>\n<li>[<strong>closed</strong>] parse full mime types <a href=\"https://github.com/broofa/node-mime/issues/43\">#43</a></li>\n</ul>\n<hr>\n<h2>v1.2.8 (10/01/2013)</h2>\n<ul>\n<li>[<strong>closed</strong>] /js directory mime is application/javascript. Is it correct? <a href=\"https://github.com/broofa/node-mime/issues/47\">#47</a></li>\n<li>[<strong>closed</strong>] Add mime types for lua code. <a href=\"https://github.com/broofa/node-mime/issues/45\">#45</a></li>\n</ul>\n<hr>\n<h2>v1.2.7 (19/10/2012)</h2>\n<ul>\n<li>[<strong>closed</strong>] cannot install 1.2.7 via npm <a href=\"https://github.com/broofa/node-mime/issues/41\">#41</a></li>\n<li>[<strong>closed</strong>] Transfer ownership to @broofa <a href=\"https://github.com/broofa/node-mime/issues/36\">#36</a></li>\n<li>[<strong>closed</strong>] it's wrong to set charset to UTF-8 for text <a href=\"https://github.com/broofa/node-mime/issues/30\">#30</a></li>\n<li>[<strong>closed</strong>] Allow multiple instances of MIME types container <a href=\"https://github.com/broofa/node-mime/issues/27\">#27</a></li>\n</ul>\n<hr>\n<h2>v1.2.5 (16/02/2012)</h2>\n<ul>\n<li>[<strong>closed</strong>] When looking up a types, check hasOwnProperty <a href=\"https://github.com/broofa/node-mime/issues/23\">#23</a></li>\n<li>[<strong>closed</strong>] Bump version to 1.2.2 <a href=\"https://github.com/broofa/node-mime/issues/18\">#18</a></li>\n<li>[<strong>closed</strong>] No license <a href=\"https://github.com/broofa/node-mime/issues/16\">#16</a></li>\n<li>[<strong>closed</strong>] Some types missing that are used by html5/css3 <a href=\"https://github.com/broofa/node-mime/issues/13\">#13</a></li>\n<li>[<strong>closed</strong>] npm install fails for 1.2.1 <a href=\"https://github.com/broofa/node-mime/issues/12\">#12</a></li>\n<li>[<strong>closed</strong>] image/pjpeg + image/x-png <a href=\"https://github.com/broofa/node-mime/issues/10\">#10</a></li>\n<li>[<strong>closed</strong>] symlink <a href=\"https://github.com/broofa/node-mime/issues/8\">#8</a></li>\n<li>[<strong>closed</strong>] gzip <a href=\"https://github.com/broofa/node-mime/issues/2\">#2</a></li>\n<li>[<strong>closed</strong>] ALL CAPS filenames return incorrect mime type <a href=\"https://github.com/broofa/node-mime/issues/1\">#1</a></li>\n</ul>","docPath":"argo-ci/node_modules/superagent/node_modules/mime/changelog","proj":"argo-ci"}}

